import Vue from 'vue';
import Router from 'vue-router';
import { NotFound, NotPermission, Login } from './views/common'
import { Admin, User, Diary, Effect, MaterailMenu, UserMenuComment, SortKind, Step, Taste, Technology, Recipe, Role } from './views/admin';
import {Index, VideoMenu, AllRecipe, Navbar, ThreeMeals, FoodMenu, GourmetMaster} from './views/main';


Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '*',
      component: NotFound,
      meta: {
        title: ''
      }
    },
    {
      path: '/',
      redirect: 'request/index',
      meta: {
        title: ''
      }
    },
    {
      path: '/nopermisson',
      component: NotPermission,
      meta: {
        title: ''
      }
    },
    {
      path: '/login',
      component: Login,
      meta: {
        title: ''
      }
    },
    {
      path: '/request',
      component: Navbar,
      children: [
        {
          path: 'index',
          component: Index,
          meta: {
            title: ''
          },
        },
        {
          path: 'videoMenu',
          component: VideoMenu,
          meta: {
            title: ''
          }
        },
        {
          path: 'allRecipe',
          component: AllRecipe,
          meta: {
            title: ''
          }
        },
        {
          path: 'threeMeals',
          component: ThreeMeals,
          meta: {
            title: ''
          }
        },
        {
          path: 'foodMenu',
          component: FoodMenu,
          meta: {
            title: ''
          }
        },
        {
          path: 'gourmetMaster',
          component: GourmetMaster,
          meta: {
            title: ''
          }
        },
      ]
    },
    {
      path: '/admin',
      component: Admin,
      redirect: 'admin/user',
      children: [
        {
          path: 'user',
          component: User,
          meta: {
            title: ''
          }
        },
        {
          path: 'role',
          component: Role,
          meta: {
            title: ''
          }
        },
        {
          path: 'diary',
          component: Diary,
          meta: {
            title: ''
          }
        },
        {
          path: 'sortKind',
          component: SortKind,
          meta: {
            title: ''
          }
        },
        {
          path: 'effect',
          component: Effect,
          meta: {
            title: ''
          }
        },
        {
          path: 'materailMenu',
          component: MaterailMenu,
          meta: {
            title: ''
          }
        },
        {
          path: 'userMenuComment',
          component: UserMenuComment,
          meta: {
            title: ''
          }
        },
        {
          path: 'recipe',
          component: Recipe,
          meta: {
            title: ''
          }
        },
        {
          path: 'step',
          component: Step,
          meta: {
            title: ''
          }
        },
        {
          path: 'taste',
          component: Taste,
          meta: {
            title: ''
          }
        },
        {
          path: 'Technology',
          component: Technology,
          meta: {
            title: ''
          }
        },
      ],
    }
  ],
});
